import React, { useEffect, useState, useCallback } from "react";
import Button from 'react-bootstrap/Button';
import axiosInstance from "../axiosConfig";
import { Link, useLocation, useNavigate, useParams } from 'react-router-dom';

function BoardDetail() {
    const [boardDetail, setBoardDetail] = useState(null);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const [isOwner, setIsOwner] = useState(false);
    const [replyList, setReplyList] = useState([]);
    const [newReplyContent, setNewReplyContent] = useState('');
    const [newRereplyContent, setNewRereplyContent] = useState('');
    const [selectedReplyId, setSelectedReplyId] = useState(null);
    const [editingReplyId, setEditingReplyId] = useState(null);
    const [editingReplyContent, setEditingReplyContent] = useState('');
    const [editingRereplyId, setEditingRereplyId] = useState(null);
    const [editingRereplyContent, setEditingRereplyContent] = useState('');
    const location = useLocation();
    const currentUserMemberId = localStorage.getItem('username');
    const navigate = useNavigate();
    const { boardId } = useParams();

    const BackPage = () => {
        const onClickBtn = () => {
            navigate(-1);
        };
        return (
            <Button variant="secondary" onClick={onClickBtn}> </Button>
        );
    };

    const getQueryParams = (search) => {
        return new URLSearchParams(search);
    };

    const fetchBoardDetail = useCallback(async () => {
        try {
            const queryParams = getQueryParams(location.search);
            const boardIdFromQuery = queryParams.get('boardId');
            const response = await axiosInstance.get(`/ex01/board/detail?boardId=${boardIdFromQuery}`);
            console.log('API :', response.data);
            setBoardDetail(response.data);
            setIsOwner(response.data.memberId === currentUserMemberId);
        } catch (error) {
            setError(error);
        } finally {
            setLoading(false);
        }
    }, [currentUserMemberId, location.search]);

    const fetchRereplies = async (replyId) => {
        try {
            console.log(`  : /ex01/rereply/${replyId}`);
            const response = await axiosInstance.get(`/ex01/rereply/${replyId}`);
            console.log(' API :', response.data); //    
            return response.data;
        } catch (error) {
            console.error('  :', error);
            return [];
        }
    };

    const fetchReplies = useCallback(async (boardId) => {
        if (!boardId) {
            console.error('boardId .');
            return; // Ensure boardId is not null
        }
        try {
            console.log(`  : /ex01/reply/${boardId}`);
            const response = await axiosInstance.get(`/ex01/reply/${boardId}`);
            console.log(' API :', response.data); //    

            const repliesWithRereplies = await Promise.all(
                response.data.map(async (reply) => {
                    const rereplies = await fetchRereplies(reply.replyId);
                    return { ...reply, rereplies };
                })
            );
            
            setReplyList(repliesWithRereplies);
        } catch (error) {
            console.error('  :', error);
        }
    }, []);

    useEffect(() => {
        fetchBoardDetail();
    }, [fetchBoardDetail]);

    useEffect(() => {
        if (boardDetail) {
            fetchReplies(boardDetail.boardId);
        }
    }, [boardDetail, fetchReplies]);

    const handleDeleteButtonClick = async () => {
        const confirmDelete = window.confirm(" ?");
        if (!confirmDelete) return;

        try {
            await axiosInstance.post(`/ex01/board/delete?boardId=${boardDetail.boardId}`);
            alert('  !');
            navigate(-1);
        } catch (error) {
            console.error(':', error);
            setError('    .');
        }
    };

    const handleNewReplySubmit = async (e) => {
        e.preventDefault();
        try {
            const response = await axiosInstance.post('/ex01/reply', {
                boardId: boardDetail.boardId,
                replyContent: newReplyContent,
                memberId: currentUserMemberId
            });
            if (response.status === 200) {
                setNewReplyContent('');
                fetchReplies(boardDetail.boardId);
            }
        } catch (error) {
            console.error('  :', error);
        }
    };

    const handleNewRereplySubmit = async (e, replyId) => {
        e.preventDefault();
        try {
            const response = await axiosInstance.post('/ex01/rereply', {
                replyId: replyId,
                rereplyContent: newRereplyContent,
                memberId: currentUserMemberId
            });
            if (response.status === 200) {
                console.log('  :', response.data);
                setNewRereplyContent('');
                fetchReplies(boardDetail.boardId); //    .
            }
        } catch (error) {
            console.error('  :', error);
        }
    };

    const handleReplyDelete = async (replyId) => {
        try {
            const response = await axiosInstance.delete(`/ex01/reply/${replyId}/${boardId}`);
            if (response.status === 200) {
                console.log('  :', response.data);
                fetchReplies(boardDetail.boardId); //    .
            }
        } catch (error) {
            console.error('  :', error);
        }
    };

    const handleRereplyDelete = async (rereplyId) => {
        try {
            const response = await axiosInstance.delete(`/ex01/rereply/${rereplyId}`);
            if (response.status === 200) {
                console.log('  :', response.data);
                fetchReplies(boardDetail.boardId); //    .
            }
        } catch (error) {
            console.error('  :', error);
        }
    };

    const handleFileDownload = async (attachId, attachRealName, attachExtension) => {
        const token = localStorage.getItem('token');
        if (!token) {
            alert(' !');
            return;
        }
        
        try {
            const response = await axiosInstance.get(`/ex01/board/download?attachId=${attachId}`, {
                responseType: 'blob',
                headers: {
                    'Authorization': `Bearer ${token}`
                }
            });
    
            const url = window.URL.createObjectURL(new Blob([response.data]));
            const link = document.createElement('a');
            link.href = url;
            link.setAttribute('download', `${attachRealName}.${attachExtension}`);
            document.body.appendChild(link);
            link.click();
            document.body.removeChild(link);
        } catch (error) {
            console.error('  :', error);
        }
    };

    const handleReplyEdit = (replyId, replyContent) => {
        setEditingReplyId(replyId);
        setEditingReplyContent(replyContent);
    };

    const handleRereplyEdit = (rereplyId, rereplyContent) => {
        setEditingRereplyId(rereplyId);
        setEditingRereplyContent(rereplyContent);
    };

    const handleReplyUpdateSubmit = async (e) => {
        e.preventDefault();
        try {
            const response = await axiosInstance.put(`/ex01/reply/${editingReplyId}`, editingReplyContent);
            if (response.status === 200) {
                console.log('  :', response.data);
                setEditingReplyId(null);
                setEditingReplyContent('');
                fetchReplies(boardDetail.boardId);
            }
        } catch (error) {
            console.error('  :', error);
        }
    };

    const handleRereplyUpdateSubmit = async (e) => {
        e.preventDefault();
        try {
            const response = await axiosInstance.put(`/ex01/rereply/${editingRereplyId}`, editingRereplyContent);
            if (response.status === 200) {
                console.log('  :', response.data);
                setEditingRereplyId(null);
                setEditingRereplyContent('');
                fetchReplies(boardDetail.boardId);
            }
        } catch (error) {
            console.error('  :', error);
        }
    };

    //      
    if (loading) {
        return <div>Loading...</div>;
    }

    //      
    if (error) {
        return <div>Error: {error.message}</div>;
    }

    return (
        <div className="container mx-auto px-4 py-8">
            <div>
                <div className="board-name">
                    <strong><h4>{boardDetail && boardDetail.boardTitle}</h4></strong>
                </div>
                <p>: {boardDetail && boardDetail.memberId}</p>
                <div className="bg-white shadow-md rounded-lg p-6">
                    <div>
                        <textarea id='content_txt' value={boardDetail && boardDetail.boardContent} readOnly> </textarea>
                    </div>
                    {/*   */}
                    {boardDetail && boardDetail.attachVO && boardDetail.attachVO.length > 0 && (
                        <div>
                            <h5> :</h5>
                            <ul>
                                {boardDetail.attachVO.map((attach) => (
                                    <li key={attach.attachId}>
                                        <Button
                                            variant="link"
                                            onClick={() => handleFileDownload(attach.attachId, attach.attachRealName, attach.attachExtension)}
                                            style={{ padding: 0, border: 'none', textDecoration: 'none', color: '#007bff' }}
                                        >
                                            {attach.attachRealName}.{attach.attachExtension}
                                        </Button>
                                    </li>
                                ))}
                            </ul>
                        </div>
                    )}
                </div>
                {currentUserMemberId && isOwner && (
                    <div className="Insert-Button">
                        <Button variant="outline-primary">
                            <Link to={`/boardUpdate?boardId=${boardDetail.boardId}`} style={{ textDecoration: 'none', color: 'black' }}></Link>
                        </Button>
                        <Button variant="outline-primary" onClick={handleDeleteButtonClick}></Button>
                    </div>
                )}
                <br />
                <BackPage />
            </div>

            {/*   */}
            <div>
                <ul>
                    {replyList.map((reply) => (
                        <li key={reply.replyId} className="comment-container">
                            <div className="comment-header">
                                <p><strong>{reply.memberId}</strong></p>
                                <div>
                                    {currentUserMemberId === reply.memberId && (
                                        <>
                                            <Button variant="outline-danger" size="sm" onClick={() => handleReplyDelete(reply.replyId)}></Button>
                                            <Button variant="outline-primary" size="sm" onClick={() => handleReplyEdit(reply.replyId, reply.replyContent)}></Button>
                                        </>
                                    )}
                                    {currentUserMemberId && (
                                        <Button variant="outline-primary" size="sm" onClick={() => setSelectedReplyId(reply.replyId)}> </Button>
                                    )}
                                </div>
                            </div>
                            <div className="comment-content">
                                {editingReplyId === reply.replyId ? (
                                    <form onSubmit={handleReplyUpdateSubmit}>
                                        <input
                                            type="text"
                                            value={editingReplyContent}
                                            onChange={(e) => setEditingReplyContent(e.target.value)}
                                            required
                                        />
                                        <Button type="submit"> </Button>
                                    </form>
                                ) : (
                                    <p>{reply.replyContent}</p>
                                )}
                            </div>
                            {selectedReplyId === reply.replyId && (
                                <div className="reply-input">
                                    <input
                                        type="text"
                                        value={newRereplyContent}
                                        onChange={(e) => setNewRereplyContent(e.target.value)}
                                        placeholder=" "
                                        required
                                    />
                                    <Button type="submit" onClick={(e) => handleNewRereplySubmit(e, reply.replyId)}> </Button>
                                </div>
                            )}
                            <ul className="reply-container">
                                {reply.rereplies && reply.rereplies.map((rereply) => (
                                    <li key={rereply.rereplyId} className="comment-container">
                                        <div className="comment-header">
                                            <p><strong>{rereply.memberId}</strong></p>
                                            <div>
                                                {currentUserMemberId === rereply.memberId && (
                                                    <>
                                                        <Button variant="outline-danger" size="sm" onClick={() => handleRereplyDelete(rereply.rereplyId)}></Button>
                                                        <Button variant="outline-primary" size="sm" onClick={() => handleRereplyEdit(rereply.rereplyId, rereply.rereplyContent)}></Button>
                                                    </>
                                                )}
                                            </div>
                                        </div>
                                        <div className="comment-content">
                                            {editingRereplyId === rereply.rereplyId ? (
                                                <form onSubmit={handleRereplyUpdateSubmit}>
                                                    <input
                                                        type="text"
                                                        value={editingRereplyContent}
                                                        onChange={(e) => setEditingRereplyContent(e.target.value)}
                                                        required
                                                    />
                                                    <Button type="submit"> </Button>
                                                </form>
                                            ) : (
                                                <p>{rereply.rereplyContent}</p>
                                            )}
                                        </div>
                                    </li>
                                ))}
                            </ul>
                        </li>
                    ))}
                </ul>
            </div>
            {/*    */}
            {currentUserMemberId && (
                <div>
                    <form onSubmit={handleNewReplySubmit}>
                        <div className="reply-input">
                            <input
                                type="text"
                                value={newReplyContent}
                                onChange={(e) => setNewReplyContent(e.target.value)}
                                placeholder=" "
                                required
                            />
                            <Button type="submit"> </Button>
                        </div>
                    </form>
                </div>
            )}
        </div>
    );
}

export default BoardDetail;
