import React, { useState, useEffect } from 'react';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import Form from 'react-bootstrap/Form';
import { useNavigate } from 'react-router-dom';
import axiosInstance from '../axiosConfig';

function MemberInfo() {
    const [memberInfo, setMemberInfo] = useState(null);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const [showPasswordModal, setShowPasswordModal] = useState(false);
    const [showDeleteConfirmModal, setShowDeleteConfirmModal] = useState(false);
    const [selectedAction, setSelectedAction] = useState('');
    const [isAuthenticated, setIsAuthenticated] = useState(false);
    const navigate = useNavigate();

    const memberId = localStorage.getItem('username');

    useEffect(() => {
        const fetchMemberInfo = async () => {
            try {
                const token = localStorage.getItem('token');
                const response = await axiosInstance.get(`/ex01/member/detail/${memberId}`, {
                    headers: {
                        'Authorization': `Bearer ${token}`
                    }
                });
                setMemberInfo(response.data);
            } catch (error) {
                setError(error);
            } finally {
                setLoading(false);
            }
        };

        fetchMemberInfo();
    }, [memberId]);

    const handlePasswordModalShow = (action) => {
        setSelectedAction(action);
        setShowPasswordModal(true);
    };

    const handlePasswordModalHide = () => {
        setShowPasswordModal(false);
    };

    const handleDeleteConfirmModalHide = () => {
        setShowDeleteConfirmModal(false);
    };

    const deleteMember = async () => {
        try {
            const token = localStorage.getItem('token');
            await axiosInstance.post('/ex01/member/delete', { memberId }, {
                headers: {
                    'Authorization': `Bearer ${token}`
                }
            });
            alert('  . .');
            localStorage.removeItem('username');
            localStorage.removeItem('token');
            navigate('/'); //     
            
         window.location.reload(); //    
        } catch (error) {
            setError('    .');
            console.log('    .');
        }
    };

    if (loading) {
        return <div> ...</div>;
    }

    if (error) {
        return <div>: {error.message}</div>;
    }

    if (!memberInfo) {
        return <div>    .</div>;
    }

    return (
        <div className="member-container">
            <div className="member-card">
                <div className="member-header">
                    <h1> </h1>
                </div>
                <div className="member-profile">
                    <div className="member-avatar">
                        <svg className="avatar-icon" fill="currentColor" viewBox="0 0 24 24">
                            <path d="M12 12c2.21 0 4-1.79 4-4s-1.79-4-4-4-4 1.79-4 4 1.79 4 4 4zm0 2c-2.67 0-8 1.34-8 4v2h16v-2c0-2.66-5.33-4-8-4z" />
                        </svg>
                    </div>
                    <div className="member-profile-info">
                        <div className="member-profile-name">{memberInfo.memberId}</div>
                    </div>
                    <div className="member-profile-actions">
                        <Button className="member-button" onClick={() => handlePasswordModalShow('email')}> </Button>
                        <Button className="member-button" onClick={() => handlePasswordModalShow('password')}> </Button>
                        <Button className="member-button" onClick={() => handlePasswordModalShow('delete')}> </Button>
                    </div>
                </div>
            </div>
            <TabContent memberInfo={memberInfo} />
            <PasswordModal
                show={showPasswordModal}
                onHide={handlePasswordModalHide}
                selectedAction={selectedAction}
                memberId={memberId}
                setIsAuthenticated={setIsAuthenticated}
                setShowDeleteConfirmModal={setShowDeleteConfirmModal} //  
            />
            <DeleteConfirmModal
                show={showDeleteConfirmModal}
                onHide={handleDeleteConfirmModalHide}
                onConfirm={deleteMember}
            />
        </div>
    );
}

function TabContent({ memberInfo }) {
    const formatDate = (dateString) => {
        const date = new Date(dateString);
        const year = date.getFullYear();
        const month = String(date.getMonth() + 1).padStart(2, '0');
        const day = String(date.getDate()).padStart(2, '0');
        return `${year}-${month}-${day}`;
    };

    return (
        <div className="member-container">
            <div className="member-card">
                <div className="member-tab-content">
                    <table className="member-info-table">
                        <tbody>
                            <tr>
                                <th></th>
                                <td>{memberInfo.memberId}</td>
                            </tr>
                            <tr>
                                <th> </th>
                                <td>{memberInfo.memberEmail}</td>
                            </tr>
                            <tr>
                                <th></th>
                                <td>{formatDate(memberInfo.memberRegistDate)}</td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    );
}

function PasswordModal({ show, onHide, selectedAction, memberId, setIsAuthenticated, setShowDeleteConfirmModal }) { //  
    const [password, setPassword] = useState('');
    const [error, setError] = useState('');
    const navigate = useNavigate(); //  

    const handlePasswordChange = (event) => {
        setPassword(event.target.value);
    };

    const handlePasswordSubmit = async () => {
        try {
            const response = await axiosInstance.post('/ex01/member/check', {
                memberId,
                memberPassword: password
            });
            console.log('Response data:', response.data);
            if (response.data === 1) {
                alert(' ');
                setIsAuthenticated(true);
                onHide();
                if (selectedAction === 'delete') {
                    setShowDeleteConfirmModal(true);
                } else {
                    switch (selectedAction) {
                        case 'email':
                            navigate('/changeEmail');
                            break;
                        case 'password':
                            navigate('/changePw');
                            break;
                        default:
                            break;
                    }
                }
            } else {
                setError('  .');
            }
        } catch (error) {
            setError('  .');
        }
    };

    useEffect(() => {
        if (show) {
            setPassword('');
            setError('');
        }
    }, [show]);

    return (
        <Modal show={show} onHide={onHide} centered>
            <Modal.Header closeButton>
                <Modal.Title> </Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <Form>
                    <Form.Group className="mb-3">
                        <Form.Label></Form.Label>
                        <Form.Control
                            type="password"
                            placeholder=" "
                            value={password}
                            onChange={handlePasswordChange}
                        />
                    </Form.Group>
                    {error && <p style={{ color: 'red' }}>{error}</p>}
                </Form>
            </Modal.Body>
            <Modal.Footer>
                <Button variant="secondary" onClick={onHide}></Button>
                <Button variant="primary" onClick={handlePasswordSubmit}></Button>
            </Modal.Footer>
        </Modal>
    );
}

function DeleteConfirmModal({ show, onHide, onConfirm }) {
    return (
        <Modal show={show} onHide={onHide} centered>
            <Modal.Header closeButton>
                <Modal.Title>  </Modal.Title>
            </Modal.Header>
            <Modal.Body> ?</Modal.Body>
            <Modal.Footer>
                <Button variant="secondary" onClick={onHide}></Button>
                <Button variant="danger" onClick={onConfirm}></Button>
            </Modal.Footer>
        </Modal>
    );
}

export default MemberInfo;
